/* 공통서식 */
body{
  margin:0px; padding:0px;
  width:100%;
}
dl,dt,dd,input,label{
  margin:0px; padding:0px; 
}
ul,li{
  list-style:none;
  padding:0px; margin:0px;
}


/* 헤더영역 */
header h1{
  text-align: center;
}

header img{
  height: 50px;
  width:50px;
  margin:0px auto;
  display: block;
  padding-top:60px;
}

/* 푸터영역 */
footer{
  height:70px;
  background-color: #F5DC98;
}

footer address{
  text-align: center;
  line-height: 70px;
  color:#333;
}

/* Create 서식 */

#create{
  max-width: 400px;
  width:100%;
  margin:0px auto;
}

#create dl{
  display:block;
  text-align:Center;
  display:flex;
  flex-wrap: wrap;
}
#create dt{
  width:140px;
  padding:10px 0px;
  /* background-color: yellowgreen; */
}

#create dd{
  width:calc(100% - 140px);
  /* background-color: yellow; */
}
#create dd input,select{
  width:90%;
  padding:10px 0px;
}

#create dd input{
  border-style:none;
  border-bottom: 1px solid #333;
  /* background-color: #F5DC98; */
}
#create dd select{
  margin-top:10px;
}
#create .button{
  padding:10px 150px;
  background-color: #F5DC98;
  border-style:none;
  border-radius:5px;
  margin:20px auto;
  cursor:pointer;
  transition:.5s;
}
#create .button:hover{
  background-color: #dfbb4f;
}

/* product 서식 */
#product li{
  width:100%;
  padding:20px 0px;
  border-top:1px solid #333;
}
  #product li{
    text-align: center;
  }
  #product button{
    border:none;
    background:#fff;
    cursor:pointer;
  }

/* ul{
  display:flex;
  justify-content: center;
} */




#product li{
  display:flex;
  justify-content: center;
}
#product div img{
  width:120px;
  height:120px;
  margin-top:30px;
}
#product div:first-of-type{
  padding:0px 30px;
}
#product div button{
  font-size:20px;
  padding: 20px 0px 0px 200px;
  text-align:right;
}
#product .box p:first-of-type{
  font-size:16px;
  font-weight:bold;

}
#product .box p:nth-of-type(2){
  font-size:14px;
  line-height: 2;
}
#product .box p:nth-of-type(3){
  font-size:14px;
  line-height: 0;
  margin-bottom: 20px;
}
#product .box p:nth-of-type(4){
  width:150px;
  height:30px;
  margin:0px auto;
  border-radius: 5px;
  background-color: #F5DC98;
}
#product .box p:nth-of-type(4) a{
  text-decoration: none;
  line-height:30px;
  color:#333;
  
}





